function Welcome() {



    const [count, setCount] = useState(10)
    const ref = useRef(null)

    useEffect(() => {
        ref.current = setInterval(() => {
            setCount((c) => {
                return c - 1
            })
        }, 1000)
        return function () {
            clearInterval(ref.current)
        }
    }, [])

    useEffect(() => {
        if (count === 0) {
            clearInterval(ref.current)
        }
    }, [count])

    return (
        <div>
            count:{count}

        </div>
    )
}

export default Welcome
